export default function numberOfProducts(productsCount) {
  if (typeof (productsCount) === 'number' && Number.isFinite(productsCount)) {
    const result = productsCount;
    if ([11, 12, 13].includes(result)) {
      return `${result} `;
    }
    if (result % 10 === 1) {
      return `${result} `;
    }
    if ([2, 3, 4].includes(result % 10)) {
      return `${result} `;
    }
    return `${result} `;
  }

  return 0;
}
